  }
  
  #glass {
    position: relative;
    display: block;
    width: 300px;
    height: 500px;
    border: solid 10px #e8e4d9;
    border-top: solid 5px #e8e4d9;
    border-bottom: solid 30px #e8e4d9;
    border-radius: 5px;
    -webkit-transform: perspective(500px) rotateX(-30deg);
            transform: perspective(500px) rotateX(-30deg);
    -webkit-transform-origin: center;
            transform-origin: center;
    margin:auto;
  }
  
  #tap {
    position: relative;
    display: block;
    width: 150px;
    height: 140px;
    background: black;
    border: solid 10px #e8e4d9;
    border-top: solid 5px #e8e4d9;
    border-bottom: solid 10px #e8e4d9;
    border-radius: 5px;
    -webkit-transform: perspective(400px) rotateX(-30deg);
            transform: perspective(400px) rotateX(-30deg);
    left: 44%;
    bottom: 100%;
    right: 2.25%;
    z-index: 1;
  }
  
  #glass:before,
  #glass:after {
    content: '';
    position: absolute;
    border-radius: 10px;
    background: #fff;
    width: 10px; 
  }
  #glass:before {
    height: 20%;
    left: 10px;
    top: 5%;
    z-index: 1;
  }
  #glass:after {
    height: 70%;
    right: 10px;
    top: 5%;
  }
  #tap:before,
  #tap:after {
    content: '';
    position: absolute;
    border-radius: 10px;
    background: #fff;
    width: 10px;
  }
  #tap:before {
    height: 20%;
    left: 10px;
    top: 5%;
    z-index: 1;
  }
  #tap:after {
    height: 70%;
    right: 10px;
    top: 5%;
  }
  
  
